<script>
import { GChart } from "vue-google-charts";
import axios from "axios";

var apiKey = "Tpk_46a92f9cc23242d0985987dcf2113f5e";

//default chart format

export default {
  name: "App",
  title: "Portfolio",
  components: {
    GChart,
  },
  data: function () {
    return {
      // An array of stock symbols as str
      stocks: [],
      seen: true,
      message: "",
      chartsLib: null,
      chartData: {
        cols: [
          {
            id: "Symbols",
            label: "Symbols",
            type: "string",
          },
          {
            id: "Price",
            label: "Price",
            type: "number",
          },
        ],
        rows: [],
      },
    };
  },
  computed: {
    chartOptions() {
      if (!this.chartsLib) return null;
      return this.chartsLib.charts.Bar.convertOptions({
        chart: {
          title: "Stock Prices",
        },
        bars: "horizontal",
        hAxis: {
          title: "Price in US $",
        },
        vAxis: {
          title: "Symbol",
        },
      });
    },
  },
  methods: {
    //performs api call and starts update timer
    handleSubmit: function () {
      var iexUrl = "https://sandbox.iexapis.com/stable/stock/symbol/price";
      var stock = this.$refs.stock.value.toUpperCase();
      var options = {
        params: {
          token: apiKey,
        },
      };
      var constructedUrl = iexUrl.replace("symbol", this.$refs.stock.value);
      axios
        .get(constructedUrl, options)
        .then((res) => this.addStock(stock, res.data))
        .catch((err) => console.log(err));
      if (this.seen) {
        setInterval(this.updateStocks, 5000);
        this.seen = false;
      }
    },
    //This method takes in the symbol and price as parameters and adds them to the datasets
    addStock: function (symbol, price) {
      this.stocks.push(symbol);
      this.chartData.rows.push({
        c: [
          {
            v: symbol,
          },
          {
            v: price,
          },
        ],
      });
      // update chart
    },
    // update data and call batch request
    updateStocks: function () {
      var iexUrl =
        "https://sandbox.iexapis.com/stable/stock/market/batch?symbols=keys&types=price";
      var options = {
        params: {
          token: apiKey,
        },
      };
      var constructedUrl = iexUrl.replace("keys", this.stocks);
      axios
        .get(constructedUrl, options)
        .then((res) => this.updateList(res.data))
        .catch((err) => console.log(err));
    },
    // helper method to update values
    updateList: function (newData) {
      var keys = Object.keys(newData);
      for (var index in keys) {
        this.chartData.rows[index].c[1].v = newData[keys[index]].price;
      }
    },
    onChartReady(chart, google) {
      this.chartsLib = google;
    },
  },
};
</script>

